import React from "react"
import {
  Card,
  CardHeader,
  CardTitle,
  CardBody,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
  Label,
} from "reactstrap"
import { connect } from "react-redux"
import { addData, getData, getParentData, updateData } from "../../../redux/actions/category"
import Select from "react-select"
import "../../../assets/scss/plugins/extensions/dropzone.scss"

class EditFrom extends React.Component {
  state = {
    _id: '',
    name: '',
    img: '',
    file: null,
    parentId: '',
    isParent: false,
  }
  oldCurrectCategory = null  // for check

  componentDidMount() {
    this.props.getParentData();
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    let currectCategory = nextProps.currectCategory;
    this.oldCurrectCategory = nextProps.currectCategory;
    if(! this.oldCurrectCategory && Object.is(nextProps.currectCategory, this.oldCurrectCategory)) {
      return false;
    } 
    if(currectCategory !== null && currectCategory !== undefined) {
      if(currectCategory._id !== this.state._id) {
        this.setState({
          _id: currectCategory._id
        })
      }
      if(currectCategory.name !== this.state.name) {
        this.setState({
          name: currectCategory.name || ""
        })
      }
      if(currectCategory.imgUrl !== this.state.img) {
        this.setState({
          img: currectCategory.imgUrl
        })
      }
      if(currectCategory.parentId !== this.state.parentId) {
        this.setState({
          parentId: currectCategory.parentId
        })
      }
      if(currectCategory.isParent !== this.state.isParent) {
        this.setState({
          isParent: currectCategory.isParent
        })
      }
    }
  }

  handleSubmit = (e) => {
    e.preventDefault();
    if(this.valid(this.state)) {
      if(this.props.isEdit) {
        this.props.updateData(this.state);
        this.props.toggle('1');
      } else {
        this.props.addData(this.state);
      }
    }
  }

  handleChangeParent = (item) => {
    this.setState({
      parentId: item.value
    })
  }

  resetValues = (e) => {
    e.preventDefault();

    this.setState({
      name: '',
      parentId: '',
      file: null,
      img: ''
    })
  }

  valid = (state) => state.name !== "" && state.img !== null

  render() {
    const { img, name, parentId, isParent } = this.state;
    let { parentData } = this.props;

    let selectOption = [];
    selectOption.push({
      value: '',
      label: 'None',
    });
    // delete parent data
    if(isParent) {
      parentData = parentData.filter((elt) => elt._id !== this.state._id);
    }
    let option = parentData.map((parent) => ({
      value: parent._id,
      label: parent.name,
    }));

    selectOption = [...selectOption, ...option]

    return (
      <React.Fragment>
        <div style={{maxWidth: 700}} className='ml-auto mr-auto'>
          <Card>
            <CardHeader>
              <CardTitle>SUBJECT </CardTitle>
            </CardHeader>
            <CardBody>
              <Form>
                <Row>
                  <Col sm="12">
                    <FormGroup>
                      <Label for="category-name">Subject Name</Label>
                      <Input
                        invalid={name === ''}
                        type="text"
                        name="name"
                        id="name"
                        placeholder="Subject Name"
                        value={name}
                        onChange={(e) => this.setState({
                          name: e.target.value
                        })}
                      />
                    </FormGroup>
                  </Col>
                  <Col sm="12">
                    <FormGroup>
                      <Label for="EmailVertical">Parent</Label>
                      <Select
                        className="React"
                        classNamePrefix="select"
                        defaultValue={selectOption[0]}
                        value={selectOption.find((option) => option.value === parentId)}
                        name="parentId"
                        options={selectOption}
                        onChange={this.handleChangeParent}
                      />
                    </FormGroup>
                  </Col>
                  <Col sm="12">
                    <FormGroup className="text-center">
                    <Label for="">Subject thumbnail (The image size should be: 400 X 400)</Label>
                      <img className="img-fluid" src={img} alt={name} />
                      <div className="d-flex flex-wrap justify-content-between mt-2">
                        <label
                          className="btn btn-flat-primary"
                          htmlFor="update-image"
                          color="primary">
                          Upload Image
                          <input
                            type="file"
                            id="update-image"
                            hidden
                            onChange={e =>{
                              console.log(e.target.files);
                              if(e.target.files[0]) {
                                this.setState({
                                  img: URL.createObjectURL(e.target.files[0]),
                                  file: e.target.files[0]
                                })
                              }
                            }}
                          />
                        </label>
                        <Button
                          color="flat-danger"
                          onClick={() => this.setState({ img: "", file: null })}>
                          Remove Image
                        </Button>
                      </div>
                    </FormGroup>
                  </Col>
                  <Col sm="12">
                    <FormGroup>
                      <Button.Ripple
                        color="primary"
                        type="submit"
                        className="mr-1 mb-1"
                        onClick={this.handleSubmit}
                      >
                        Submit
                      </Button.Ripple>
                      <Button.Ripple
                        outline
                        color="warning"
                        type="reset"
                        className="mb-1"
                        onClick={this.resetValues}
                      >
                        Reset
                      </Button.Ripple>
                    </FormGroup>
                  </Col>
                </Row>
              </Form>
            </CardBody>
          </Card>
        </div>
      </React.Fragment>
    )
  }
}
const mapStateToProps = (state) => {
  return {
    parentData: state.category.allData
  }
}
  
const mapDispatchToProps = {
  addData, 
  getData, 
  getParentData,
  updateData
}

export default connect(mapStateToProps, mapDispatchToProps)(EditFrom)